.Button-container {
  background-color: #5cdb95;
  color: white;
  font-size: 2em;
  padding: 0.1em 0.25em 0.25em .25em;
  cursor: pointer; }
  .Button-container.Back {
    width: 4em;
    height: 2em;
    padding-top: 0.75em;
    padding-bottom: 0; }

.Fixed-button-container {
  position: fixed;
  top: 50%;
  background-color: #5cdb95;
  color: white;
  font-size: 4em;
  padding: 0.1em 0.25em 0.25em .25em;
  cursor: pointer; }
  .Fixed-button-container.Next {
    right: 0; }
  .Fixed-button-container.Previous {
    left: 0; }
  .Fixed-button-container ul {
    padding: inherit;
    margin: inherit; }
    .Fixed-button-container ul li {
      padding: 0;
      list-style: none; }
      .Fixed-button-container ul li a {
        text-decoration: none;
        color: white; }



